import React, { useState } from "react";
import "./index.css";
import AddIcon from '@material-ui/icons/Add';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";

const CreateNote =(props)=>{
    
    const [Data,setData] = useState({
        title :"",
        text :"",
    });
    
    const getData =()=>{
        props.passNote(Data); 
        setData({
            title:"",
            text:"",
        });    
    };
    
    const getNote=(e)=>{
       
        const {name,value} =e.target;
        
        setData((preData)=>{
            return{
                ...preData,
                [name] :value,
            };
        });
    };
    
    
    return(
        <>
            <div className="container-fluid">
                <div className="row">
                <div className="col-lg-3 col-md-3 col-12text-center main mx-auto">
                    <input 
                        className="inp11" 
                        placeholder="title" 
                        name="title" 
                        value={Data.title}
                        onChange={getNote}
                        >
                    </input>
                    <textarea 
                        className="inp22" 
                        rows="3" 
                        placeholder="create a note" 
                        name="text" 
                        value={Data.text}
                        onChange={getNote}
                        >
                    </textarea>
                    <button 
                        className="btn1" 
                        onClick={getData}
                        >
                        <AddIcon />
                    </button>
                </div>
                </div>
            </div>
        </>
    );
}

export default CreateNote;