import React from "react";
import "./index.css";
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";

const Note =(props)=>{
    const deleteNote=()=>{
        props.deleteItem(props.id);
    };
    
    return(
        <>    
            <div className="main1">
                <h3>{props.title}</h3>
                <br />
                <p>{props.text}</p>
                <button className="btn1" onClick={deleteNote}><DeleteOutlineIcon /></button>
            </div>
        </>
    );
};

export default Note;