import React, { useState } from "react";
import Header from "./Header";
import CreateNote from "./CreateNote";
import Footer from "./Footer";
import Note from "./Note";



const App =()=>{
    
    const [addItem,setaddItem] = useState([]);
    
    const addNote=(Data)=>{
        // alert("hi sumit");
        setaddItem((preData)=>{
            return[...preData,Data];
        });
    };
    const onDelete=(id)=>{
        setaddItem((old_data)=>
            old_data.filter((curr,index) => {
                return index !==id; 
            })
        );
        };
    return(
        <>
            <Header />
            <CreateNote passNote ={addNote} />
            {addItem.map((val, index)=>{
                return<Note 
                    key={index}
                    id={index}
                    title={val.title}
                    text={val.text}
                    deleteItem ={onDelete}
                />
            })}
            <Footer />
        </>
    );
}

export default App;